const AEqualB = (objectA: any, objectB: any) => {
  // 比较A的键是不是B里面都有
  let flagA = 0
  for (const itemA in objectA) {
    console.log(itemA)
    let flag = 0
    for (const itemB in objectB) {
      console.log(itemB)
      if (itemA === itemB) {
        flag = 1
        break
      }
    }
    if (flag == 0) {
      flagA = 1
    }
  }
  if (flagA === 1) {
    return false
  } else {
    return true
  }
}

const keyIsEqual = (objectA: any, objectB: any) => {
  // 比较两次
  const a = AEqualB(objectA, objectB)
  const b = AEqualB(objectB, objectA)
  return a && b
}

const A = {
  a: 0,
  b: 0,
  c: 0
}
const B = {
  a: 1,
  b: 2,
  c: 3
}

export default keyIsEqual
